import useSocialFeed from '../../hooks/socialFeed/useSocialFeed';
import { useEffect, useRef, useState } from 'react';
import { useIntersection } from '../../hooks/intersection/useIntersection';
import { PostContent } from '@youfoundation/dotyoucore-js';
import { PostFileVm } from '../../provider/transit/ExternalPostsDataProvider';
import { flattenInfinteData } from '../../helpers/common';
import LoadingParagraph from '../../components/ui/LoadingParagraph/LoadingParagraph';
import { useNavigate } from 'react-router-dom';
import useTabs from '../../hooks/tabs/useTabs';
import NavPills from '../../components/ui/Submenu/NavPills';
import ProfileHero from '../Home/Common/ProfileHero/ProfileHero';
import PostComposer from '../../components/SocialFeed/PostComposer';
import PostTeaserCard from '../../components/SocialFeed/PostTeaserCard';

const PAGE_SIZE = 10;

const Feed = ({ className }: { className?: string }) => {
  const { tabs, isTabs } = useTabs();
  const navigate = useNavigate();
  const [forceRender, setForceRender] = useState(false);

  const {
    data: posts,
    hasNextPage: hasMorePosts,
    isLoading: postsLoading,
    fetchNextPage,
  } = useSocialFeed({ pageSize: PAGE_SIZE }).fetchAll;

  const loadMoreRef = useRef<HTMLDivElement>(null);

  useIntersection(
    hasMorePosts ? loadMoreRef : undefined,
    () => {
      fetchNextPage();
    },
    true
  );

  // Flatten all pages, sorted descending and slice on the max number expected
  const flattenedPosts = flattenInfinteData<PostFileVm<PostContent>>(
    posts,
    PAGE_SIZE,
    (a, b) => b.content?.dateUnixTime - a.content?.dateUnixTime
  );

  useEffect(() => {
    if (forceRender) {
      setForceRender(false);
    }
  }, [forceRender]);

  return (
    <>
      <ProfileHero hideLinks={true} />
      <section className="body-font text-gray-600 dark:text-gray-400">
        <>
          {isTabs ? (
            <div className="bg-slate-500 bg-opacity-5 dark:bg-slate-500 dark:bg-opacity-5">
              <div className="container mx-auto overflow-auto px-5 py-4">
                <NavPills
                  onChange={(newTab) => navigate(`/home/${newTab}`)}
                  items={tabs.map((tab) => {
                    return { ...tab, isActive: tab.key === 'feed' };
                  })}
                  disallowWrap={true}
                />
              </div>
            </div>
          ) : null}
          <div className="container mx-auto px-5">
            <div className="py-10">
              <div className={className}>
                <div className="-mx-2 flex max-w-6xl flex-col lg:flex-row xl:-mx-4">
                  <div className="py-2 px-2 lg:w-2/3 xl:px-4">
                    <PostComposer onPost={() => setForceRender(true)} />
                    {postsLoading ? (
                      <div className="-mx-4">
                        <LoadingParagraph className="m-4 h-10" />
                        <LoadingParagraph className="m-4 h-10" />
                        <LoadingParagraph className="m-4 h-10" />
                      </div>
                    ) : flattenedPosts?.length ? (
                      <>
                        {flattenedPosts.map((post) => {
                          return (
                            <PostTeaserCard
                              key={post.fileId || post.content.id}
                              postFile={post}
                              dotYouId={post.dotYouId}
                              className={`my-2 ${
                                !post.fileId ? 'overflow-hidden bg-slate-100 dark:bg-slate-600' : ''
                              }`}
                            />
                          );
                        })}
                        <div ref={loadMoreRef} className="h-1 w-full"></div>
                      </>
                    ) : null}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      </section>
    </>
  );
};

export default Feed;
