import { useInfiniteQuery } from '@tanstack/react-query';
import { PostContent } from '@youfoundation/dotyoucore-js';
import ExternalPostsDataProvider, {
  MultiRequestCursoredResult,
  PostFileVm,
} from '../../provider/transit/ExternalPostsDataProvider';
import useAuth from '../auth/useAuth';
import useConnections from '../connections/useConnections';

const useSocialFeed = ({ pageSize = 10 }: { pageSize: number }) => {
  const { getSharedSecret } = useAuth();
  const { data: connections, isFetched: connectionsFetched } = useConnections({
    pageSize: 100,
  }).fetch;

  const fetchAll = async ({
    pageParam,
  }: {
    pageParam: Record<string, Record<string, string>>;
  }): Promise<MultiRequestCursoredResult<PostFileVm<PostContent>[]>> => {
    const externalPostDataProvider = ExternalPostsDataProvider.getInstance(getSharedSecret());
    const response = await externalPostDataProvider.getRecentsFromConnections(
      connections?.map((connection) => connection.dotYouId).filter(Boolean) as string[],
      pageSize,
      pageParam,
      true
    );

    return response;
  };

  return {
    fetchAll: useInfiniteQuery(['social-feeds'], ({ pageParam }) => fetchAll({ pageParam }), {
      getNextPageParam: (lastPage) =>
        (lastPage?.results?.length >= 1 && lastPage?.cursorState) ?? undefined,
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      staleTime: Infinity,
      enabled: connectionsFetched,
    }),
  };
};

export default useSocialFeed;
