import { useQuery } from '@tanstack/react-query';
import { PublicClient } from '@youfoundation/dotyoucore-js';
import useAuth from '../auth/useAuth';

const useConnections = ({ pageSize }: { pageSize?: number }) => {
  const { getSharedSecret, getApiType } = useAuth();
  const sharedSecret = getSharedSecret();

  const client = new PublicClient({ api: getApiType(), sharedSecret: sharedSecret });
  const fetchConnections = async ({ pageSize }: { pageSize?: number }) => {
    if (!sharedSecret) {
      return;
    }
    try {
      return (
        await client.circleNetworkReadOnlyProvider.getConnections({
          pageNumber: 1,
          pageSize: pageSize ?? 10,
        })
      ).results;
    } catch (ex) {
      return;
    }
  };

  return {
    fetch: useQuery(['connections', pageSize], () => fetchConnections({ pageSize }), {
      enabled: !!sharedSecret,
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      retry: false,
    }),
  };
};

export default useConnections;
