import { PostContent, Article, Image as ImageType, PostFile } from '@youfoundation/dotyoucore-js';
import { FC } from 'react';
import useChannel from '../../hooks/blog/useChannel';
import useSocialChannel from '../../hooks/socialFeed/useSocialChannel';
import Bubble from '../ui/Icons/Bubble/Bubble';
import Triangle from '../ui/Icons/Triangle/Triangle';

import PostMeta from '../Blog/Common/Blocks/Meta/Meta';
import FakeAnchor from '../ui/Buttons/FakeAnchor';
import AuthorImage from '../Blog/Common/Blocks/Author/Image';
import AuthorName from '../Blog/Common/Blocks/Author/Name';
import PrimaryImage from '../Blog/Common/Blocks/PrimaryImage/PrimaryImage';
import ImageGallery from '../Blog/Common/Blocks/ImageGallery/ImageGallery';
import { ellipsisAtMaxChar } from '../../helpers/common';
import { t } from '../../helpers/i18n/dictionary';

interface PostTeaserCardProps {
  className?: string;
  postFile: PostFile<PostContent>;
  isDraft?: boolean;
  dotYouId?: string;
}

const PostTeaserCard: FC<PostTeaserCardProps> = ({
  className,
  dotYouId,
  postFile: { content: post, previewThumbnail },
}) => {
  const isExternal = !dotYouId || dotYouId !== window.location.hostname;

  const { data: externalChannel } = useSocialChannel({
    dotYouId: isExternal ? dotYouId : undefined,
    channelId: post.channelId,
  }).fetch;
  const { data: internalChannel } = useChannel({ channelId: post.channelId });

  const channel = externalChannel || internalChannel;

  const imageFileIds = (post as ImageType).imageFileIds;
  const postPath = isExternal
    ? `https://${dotYouId}/home/posts/${channel ? channel.slug : 'public-posts'}/${
        post.slug ?? post.id
      }`
    : `/home/posts/${channel ? channel.slug : 'public-posts'}/${post.slug ?? post.id}`;

  return (
    <div className={`w-full rounded-lg ${className ?? ''}`}>
      <FakeAnchor
        href={postPath}
        target="__blank"
        rel="nofollow noreferrer"
        className="relative flex h-full flex-col rounded-lg border border-gray-200 border-opacity-60 transition-colors hover:shadow-md dark:border-gray-800 hover:dark:shadow-slate-600 sm:flex-row"
      >
        <div className="py-4 pl-4">
          <AuthorImage dotYouId={dotYouId} className="h-[5rem] w-[5rem] rounded-full" />
        </div>
        <div className="flex max-w-lg flex-grow flex-col py-3 px-4">
          <div className="pr-6">
            <div className="mb-1 flex flex-col text-foreground text-opacity-60 md:flex-row md:items-center">
              <h2>
                <AuthorName dotYouId={dotYouId} />
              </h2>
              <span className="hidden px-2 leading-4 md:block">Â·</span>
              {channel && post ? <PostMeta post={post} channel={channel} /> : null}
            </div>

            {/* Type specific content */}
            {post.type === 'Article' ? (
              <>
                <h1 className="text-foreground text-opacity-80">{post.caption}</h1>
                <p className="leading-relaxed text-foreground text-opacity-70">
                  {ellipsisAtMaxChar((post as Article).abstract, 140)}{' '}
                  {(post as Article)?.abstract?.length > 140 ? (
                    <span className="text-button">{t('Read more')}</span>
                  ) : null}
                </p>
              </>
            ) : (
              <h1 className="text-foreground text-opacity-70">{post.caption}</h1>
            )}
          </div>

          {post.primaryImageFileId &&
            (imageFileIds && imageFileIds.length > 1 ? (
              <ImageGallery
                dotYouId={dotYouId}
                channelId={post.channelId}
                fileIds={imageFileIds}
                className="my-4"
                postUrl={postPath}
                previewThumbnail={previewThumbnail}
              />
            ) : (
              <div className={`relative py-4 pr-6`}>
                <PrimaryImage
                  post={post}
                  dotYouId={dotYouId}
                  className={`${'sm:rounded-lg'} ${
                    post.type === 'Image' || post.type === 'Video'
                      ? 'h-full max-h-[30rem] w-auto'
                      : 'h-64 w-full md:h-36 lg:h-48'
                  } ${post.type === 'Video' ? 'aspect-video' : ''}`}
                  postUrl={postPath}
                  previewThumbnail={previewThumbnail}
                />
                {post.type === 'Video' && (
                  <>
                    <Triangle className="text-color absolute top-[calc(50%-2.5rem)] left-[calc(50%-2.5rem)] h-20 w-20" />
                  </>
                )}
              </div>
            ))}

          {/* Interacts */}
          <div className="mt-auto flex items-center pr-6 text-sm text-foreground text-opacity-10">
            <span className="inline-flex items-center">
              <Bubble className="mr-1 inline-block h-4 w-4" /> 6
            </span>
          </div>
        </div>
      </FakeAnchor>
    </div>
  );
};

export default PostTeaserCard;
