import { useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import useAuth from '../../../hooks/auth/useAuth';
import InfoDialog from '../../Dialog/InfoDialog/InfoDialog';

const FollowLink = ({ className }: { className: string }) => {
  const { isOwner } = useAuth();

  const [isOpen, setIsOpen] = useState(false);

  if (isOwner) {
    return null;
  }

  return (
    <>
      <a
        className={`block flex-grow cursor-pointer rounded-md bg-button px-3 py-2 text-center text-button-text hover:bg-button hover:grayscale-[20%] hover:filter sm:flex-grow-0 md:text-left ${
          className ?? ''
        }`}
        onClick={() => setIsOpen(true)}
      >
        {t('Follow')}
      </a>
      <InfoDialog
        isOpen={isOpen}
        onConfirm={() => setIsOpen(false)}
        onCancel={() => setIsOpen(false)}
        title={t('Coming soon')}
        children={
          <p>
            {t(
              'Unfortunately it is not possible yet to follow someone. This feature will come soon'
            )}
          </p>
        }
        confirmText={t('Ok')}
      />
    </>
  );
};

export default FollowLink;
