import { useQuery } from '@tanstack/react-query';
import ExternalPostsDataProvider from '../../provider/transit/ExternalPostsDataProvider';
import useAuth from '../auth/useAuth';

interface useSocialChannelProps {
  dotYouId?: string;
  channelId?: string;
}

const useSocialChannel = ({ dotYouId, channelId }: useSocialChannelProps) => {
  const { getSharedSecret } = useAuth();

  const fetch = async ({ dotYouId, channelId }: useSocialChannelProps) => {
    if (!dotYouId || !channelId) {
      return;
    }

    const externalPostDataProvider = ExternalPostsDataProvider.getInstance(getSharedSecret());
    return await externalPostDataProvider.getChannel(dotYouId, channelId);
  };

  return {
    fetch: useQuery(['channel', dotYouId, channelId], () => fetch({ dotYouId, channelId }), {
      enabled: !!dotYouId && !!channelId,
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      staleTime: Infinity,
    }),
  };
};

export default useSocialChannel;
