import { useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import useAuth from '../../../hooks/auth/useAuth';
import LoginDialog from '../../Auth/ProfileNav/LoginDialog';

const ConnectLink = ({ className }: { className: string }) => {
  const { isAuthenticated, getIdentity, isOwner } = useAuth();
  const [isLogin, setIsLogin] = useState(false);

  if (isOwner) {
    return null;
  }

  return (
    <>
      <a
        className={`block flex-grow cursor-pointer rounded-md bg-button px-3 py-2 text-center text-button-text hover:grayscale-[20%] hover:filter sm:flex-grow-0 md:text-left ${
          className ?? ''
        }`}
        href={
          isAuthenticated && !isOwner
            ? `https://${getIdentity()}/owner/connections/${window.location.host}`
            : undefined
        }
        onClick={!isAuthenticated ? () => setIsLogin(true) : undefined}
      >
        {t('Connect')}
      </a>

      <LoginDialog
        isOpen={isLogin}
        onCancel={() => setIsLogin(false)}
        title={t('Login required')}
      />
    </>
  );
};

export default ConnectLink;
