import {
  FileQueryParams,
  BlogConfig,
  PostFile,
  PostContent,
  GetBatchQueryResultOptions,
  CursoredResult,
  getChannelDrive,
  ChannelDefinition,
  PublicClient,
} from '@youfoundation/dotyoucore-js';
import { OwnerProviderBase } from '../OwnerProviderBase';
import TransitProvider from './TransitProvider';

export interface MultiRequestCursoredResult<T> {
  results: T;
  cursorState: Record<string, Record<string, string>>;
}

const _internalChannelCache = new Map<string, Promise<ChannelDefinition[]>>();

export interface PostFileVm<T extends PostContent> extends PostFile<T> {
  dotYouId: string;
}

export default class ExternalPostsDataProvider extends OwnerProviderBase {
  private static instance: ExternalPostsDataProvider;
  private transitProvider: TransitProvider;

  constructor(sharedSecret: Uint8Array) {
    super(sharedSecret);

    this.transitProvider = TransitProvider.getInstance(sharedSecret);
  }

  async getRecentsFromConnections(
    dotYouIds: string[],
    pageSize = 10,
    cursorState?: Record<string, Record<string, string>>,
    includeOwn?: boolean
  ): Promise<MultiRequestCursoredResult<PostFileVm<PostContent>[]>> {
    const client = new PublicClient({
      api: super.getType(),
      sharedSecret: super.getSharedSecret(),
    });
    const allCursors: Record<string, Record<string, string>> = {};

    const resultPerConnection = await Promise.all(
      dotYouIds.map(async (dotYouId) => {
        const channels = await this.getChannels(dotYouId);
        allCursors[dotYouId] = {};

        const resultsPerChannel = await Promise.all(
          channels.map(async (channel) => {
            const result = await this.getRecents(
              dotYouId,
              pageSize,
              cursorState?.[dotYouId]?.[channel.channelId],
              channel.channelId
            );
            allCursors[dotYouId][channel.channelId] = result.cursorState;
            return result.results;
          })
        );

        return resultsPerChannel;
      })
    );

    if (includeOwn) {
      const ownerDotYou = window.location.hostname;
      const resultOfOwn = await client.blogPostReadonlyProvider.getRecentPosts(
        undefined,
        pageSize,
        cursorState?.[ownerDotYou]
      );

      allCursors[ownerDotYou] = resultOfOwn.cursorState;
      resultPerConnection.push([
        resultOfOwn.results
          .filter((file) => !file.isDraft)
          .map((postFile) => {
            return { ...postFile, dotYouId: ownerDotYou } as PostFileVm<PostContent>;
          }),
      ]);
    }

    return { results: resultPerConnection.flat(2), cursorState: allCursors };
  }

  async getChannels(dotYouId: string) {
    const cacheKey = `${dotYouId}`;
    if (_internalChannelCache.has(cacheKey)) {
      const cacheData = await _internalChannelCache.get(cacheKey);
      if (cacheData) {
        return cacheData;
      }
    }

    const drives = await this.transitProvider.GetDrivesByType(
      BlogConfig.DriveType,
      1,
      1000,
      dotYouId
    );
    const channelHeaders = drives.results.map((drive) => {
      return {
        id: drive.targetDriveInfo.alias,
        name: drive.name,
      };
    });

    const promise = (async () => {
      return (
        await Promise.all(
          channelHeaders.map(async (header) => {
            const definition = await this.getChannel(dotYouId, header.id);
            return definition;
          })
        )
      ).filter((channel) => channel !== undefined) as ChannelDefinition[];
    })();

    _internalChannelCache.set(cacheKey, promise);

    return await promise;
  }

  async getRecents(
    dotYouId: string,
    maxRecords = 10,
    cursorState?: string,
    channelId?: string
  ): Promise<CursoredResult<PostFileVm<PostContent>[]>> {
    const targetDrive = channelId ? getChannelDrive(channelId) : BlogConfig.PublicChannelDrive;

    const queryParams: FileQueryParams = {
      targetDrive: targetDrive,
      fileType: [BlogConfig.PostFileType],
    };

    const ro: GetBatchQueryResultOptions = {
      cursorState: cursorState,
      maxRecords: maxRecords,
      includeMetadataHeader: true,
    };

    const result = await this.transitProvider.QueryBatch(dotYouId, queryParams, ro);

    const posts = (
      await Promise.all(
        result.searchResults.map(async (dsr) => {
          return {
            fileId: dsr.fileId,
            acl: dsr.serverMetadata?.accessControlList,
            content: await this.transitProvider.GetPayload<PostContent>(
              dotYouId,
              targetDrive,
              dsr.fileId,
              dsr.fileMetadata,
              dsr.sharedSecretEncryptedKeyHeader,
              result.includeMetadataHeader
            ),
            dotYouId: dotYouId,
            previewThumbnail: dsr.fileMetadata.appData.previewThumbnail,
            additionalThumbnails: dsr.fileMetadata.appData.additionalThumbnails,
          } as PostFileVm<PostContent>;
        })
      )
    ).filter((item) => !!item);

    return { cursorState: result.cursorState, results: posts };
  }

  async getChannel(dotYouId: string, channelId: string): Promise<ChannelDefinition | undefined> {
    const targetDrive = getChannelDrive(channelId);

    const queryParams: FileQueryParams = {
      targetDrive: targetDrive,
      fileType: [BlogConfig.ChannelDefinitionFileType],
    };

    const ro: GetBatchQueryResultOptions = {
      cursorState: undefined,
      maxRecords: 1,
      includeMetadataHeader: true,
    };

    const response = await this.transitProvider.QueryBatch(dotYouId, queryParams, ro);

    try {
      if (response.searchResults.length == 1) {
        const dsr = response.searchResults[0];
        return await this.transitProvider.GetPayload<ChannelDefinition>(
          dotYouId,
          targetDrive,
          dsr.fileId,
          dsr.fileMetadata,
          dsr.sharedSecretEncryptedKeyHeader,
          response.includeMetadataHeader
        );
      }
    } catch (ex) {
      // Catch al, as targetDrive might be inaccesible (when it doesn't exist yet)
    }
  }

  public static getInstance(sharedSecret?: Uint8Array): ExternalPostsDataProvider {
    if (!sharedSecret) {
      throw new Error('Shared Secret not set for OwnerProvider');
    }

    if (!ExternalPostsDataProvider.instance) {
      ExternalPostsDataProvider.instance = new ExternalPostsDataProvider(sharedSecret);
    }

    return ExternalPostsDataProvider.instance;
  }
}
